@import url(https://fonts.googleapis.com/css2?family=Birthstone&family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Birthstone&family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Birthstone&family=Playfair+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* NavBar */
#navbar {
	background-color: #02111B;
	/* margin-top: 30px; */
	opacity: 0.9;
}

#navbar-logo {
	display: flex;
	align-Items: center;
}

#navbar-logo-img {
	width: 90px;
	height: 90px;
	margin-right: 2px;
}

.navbar-separator {
  border-right: 1px solid #ffffff;
}

.navbar-item {
	font-family: 'Playfair Display', serif;
	letter-spacing: 5px;
	font-size: 16px;
}

.gallery-container {
	margin-top: 50px;
	font-family: 'Birthstone', cursive;
}

.gallery-content{
	text-align: center;
}

.gallery-description {
	color:  #cc3f0c;
	font-size: 2.5vw;
	font-family: 'Birthstone', cursive;
    text-align: center;
	margin: 5px;
    letter-spacing: .10em;
	font-weight: 500;
	text-shadow: 0px 0px 1px rgba(204, 63, 12, 0.3);
}

.image{
	box-shadow: rgba(28, 41, 50, 0.4) 0px 2px 10px, rgba(28, 41, 50, 0.3) 0px 7px 15px -3px, rgba(28, 41, 50, 0.2) 0px -3px 0px inset;
	-webkit-filter: drop-shadow(0 0 0.95rem rgba(28, 41, 50, 0.5) );
	        filter: drop-shadow(0 0 0.95rem rgba(28, 41, 50, 0.5) );
	border-radius: 5px;
	margin: 20px;
	max-width: 600px;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	position: relative;
    overflow: hidden;
}

.image img{
	width: 100%;
    height: 20vw;
    object-fit: cover;
	position: relative;
	vertical-align: top;
	transition: 0.6s;
	transition-property: opacity;
}

.image:hover img{
	opacity: 1;
}

 .image .details{
	z-index: 1;
	position: absolute;
	top: 0;
	right: 0;
	color: #ffff;
	width: 100%;
	height: 100%;
}

.image .details h2{
	text-align: center;
	font-size: 3vw;
	font-weight: 500;
	margin-top: 70px;
	transition: 0.4s;
	transition-property: -webkit-transform;
	transition-property: transform;
	transition-property: transform, -webkit-transform;
}

.image:hover .details h2{
	font-size: 3.5vw;
	-webkit-transform: translateY(-30px);
	        transform: translateY(-30px);
}


.more{
	position: absolute;
	background: rgba(255, 255, 255, 0.8);
	width: 100%;
	display: flex;
	justify-content: end;
	padding: 15px;
	bottom: -70px;
	transition: 0.6s;
	opacity: 0.9;
	transition-property: bottom;
}

.image:hover .more{
	bottom: 0;
}

.more .read-more{
	color: #cc3f0c;
	text-decoration: none;
	font-size: 1.5vw;
	font-weight: 500;
	text-align: end;
}


.more .icon-links i{
	color: #000;
	font-size: 20px;
}

.more .icon-links a:not(:last-child) i{
	margin-right: 20px;
	text-align: end;
}

/* Responsive CSS */

 @media (max-width: 1080px){
	.image{
		flex: 100% 1;
		max-width: 480px;
	}
}

@media (max-width: 400px){
	.image .details p{
		font-size: 16px;
	}

	.more .read-more, .more .icon-links a i{
		font-size: 18px;
	}
} 
#container {
	width: 100vw;
	min-height: 100vh;
	background: url(/art/public/index.html/static/media/home_background.e3c09376.jpg) no-repeat center center fixed;
	background-size: auto;
	background-size: cover;
	text-align: center;
}

/* Artist title */

#title-container{
	position: absolute;
	top: 50%;
	left: 50%;
	margin: 0;
}

h1 {
    color: #eaf0ce;
    font-family: 'Playfair Display', serif;
    text-align: center;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

#title {
    font-weight: 700;
    display: inline-flex;
    margin: -5px;
    padding: 5px;
    letter-spacing: 5px;
} 

.title-letter {
    font-size: calc(6vw + 1rem);
    margin-left: -2px;
}

#sub-title {
    font-weight: 400;
    font-size: calc(0.6vw + 0.5rem);
    letter-spacing: calc(0.3vw + 0.5rem);
    text-transform: uppercase;
    position: relative;
    top: -5px;
}

/* Opacity For Start */
.title-letter, #sub-title {
  opacity: 0;   
}
.category-container {
	font-family: 'Birthstone', cursive;
    font-size: 2vw;
    cursor: pointer;
}

.category-title {
    padding-top: 50px;
    font-size: 2.5vw;
    color:  #cc3f0c;
	font-family: 'Birthstone', cursive;
    text-align: center;
	margin: 10px;
    letter-spacing: .10em;
	font-weight: 500;
	text-shadow: 0px 0px 1px rgba(204, 63, 12, 0.3);

}
.category-img-flex {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.category-img-container {
    box-shadow: rgba(28, 41, 50, 0.4) 0px 2px 10px, rgba(28, 41, 50, 0.3) 0px 7px 15px -3px, rgba(28, 41, 50, 0.2) 0px -3px 0px inset;
	-webkit-filter: drop-shadow(0 0 0.95rem rgba(28, 41, 50, 0.5) );
	        filter: drop-shadow(0 0 0.95rem rgba(28, 41, 50, 0.5) );
    color: #fff;
    position: relative;
    overflow: hidden;
    min-width: 450px;
	max-width: 400px;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
    background: #000000;
    text-align: center;
    margin: 20px
}

.category-img-container * {
    box-sizing: border-box;
}

.category-img-container img {
    opacity: 1;
    width: 100%;
    height: 30vw;
    object-fit: cover;
    transition: opacity 0.6s;
}

.category-img-container figcaption {
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 2em 1em;
    width: 100%;
    height: 50%;
}

.category-img-container figcaption::before {
    position: absolute;
    top: 50%;
    right: 30px;
    bottom: 50%;
    left: 30px;
    border-top: 1px solid rgba(255, 255, 255, 0.8);
    border-bottom: 1px solid rgba(255, 255, 255, 0.8);
    content: '';
    opacity: 0;
    background-color: #ffffff;
    transition: all 0.4s;
    transition-delay: 0.6s;
}

.category-img-container h2,
.category-img-container p {
    margin: 0 0 5px;
    opacity: 0;
    transition: opacity 0.35s,-webkit-transform 0.35s,transform 0.35s;
  }

.category-img-container h2 {
    word-spacing: -0.15em;
    font-weight: 500;
    text-transform: uppercase;
    -webkit-transform: translate3d(0%, 50%, 0);
    transform: translate3d(0%, 50%, 0);
    transition-delay: 0.3s;
}

.category-img-container5 p {
    font-weight: 500	;
    transition-delay: 0s;
}

.category-img-container a {
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    position: absolute;
    color: #fff;
}

.category-img-container5:hover img {
    opacity: 0.35;
}

.category-img-container:hover figcaption h2 {
    opacity: 1;
    -webkit-transform: translate3d(0%, 0%, 0);
    transform: translate3d(0%, 0%, 0);
    transition-delay: 0.3s;
  }

.category-img-container:hover figcaption p {
    opacity: 0.9;
    transition-delay: 0.6s;
  }

.category-img-container:hover figcaption::before {
    background: rgba(0, 0, 0, 0.3);
    top: 30px;
    bottom: 30px;
    opacity: 1;
    transition-delay: 0s;
  }
  

